* {
    margin: 0;
    padding: 0;
}

body {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif serif;
}

.title {
    text-align: center;
    color:yellow;
    display:flexbox;

}

.app {
    background-color: rgb(62, 160, 106);
    width: 95%;
    min-height: 200px;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgb(0, 0, 0, 0.75);
    display:block;
    justify-content: space-evenly;
}

.Question-section {

    position: relative;
    width: 100%;
}

.Question-text {
    color:black;
    font-size: 30px;
}

.Answer-section {
    display:flex;
    flex-direction: column;
    width: 50%;
    justify-content: space-between;
}

button{
    width: 90%;
    font-size: 16px;
    color:rgb(3, 3, 3);
    border-radius: 15px;
    display: flex;
    padding: 10px;
    justify-content:flex-start;
    align-items: center;
    border:2px solid black;
    cursor: pointer;
}

.score-section{
    display:column;
    justify-content: center;
    align-items: center;
    color:rgb(5, 48, 12);
    font-size: 25px;
}
button{
    color: rgb(137, 10, 241);
    background-color: rgb(156, 183, 233);

}
.score-section button{
    background-color: blue;
    color: aliceblue;
    width: 100px;
    align-items: flex-end;
}
.score-section button{
    display: flex;
    align-items: center;
    justify-content: center;
}
